import { Component, Input, Output, EventEmitter, OnInit, signal, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Department, DepartmentService } from '../../../services/department.service';
import { TranslationService } from '../../../services/translation.service';

@Component({
  selector: 'app-move-department-modal',
  standalone: false,
  templateUrl: './move-department-modal.component.html',
  styleUrls: ['./move-department-modal.component.css']
})
export class MoveDepartmentModalComponent implements OnInit {
  @Input() department!: Department;
  @Input() departments!: Department[];
  @Input() isOpen = false;
  @Output() close = new EventEmitter<void>();
  @Output() departmentMoved = new EventEmitter<void>();

  form!: FormGroup;
  loading = signal(false);
  error = signal<string | null>(null);
  
  availableParents = signal<Department[]>([]);

  constructor(
    private fb: FormBuilder,
    private departmentService: DepartmentService,
    private translationService: TranslationService
  ) {}

  ngOnInit(): void {
    this.initForm();
    this.filterAvailableParents();
  }

  initForm(): void {
    this.form = this.fb.group({
      newParentId: [null],
      newOrder: [0, [Validators.required, Validators.min(0)]]
    });

    if (this.department.parentId) {
      this.form.patchValue({
        newParentId: this.department.parentId
      });
    }
  }

  filterAvailableParents(): void {
    // Filter out the current department and its children from available parents
    const available = this.departments.filter(dept => 
      dept.id !== this.department.id && 
      !this.isDescendant(dept, this.department)
    );
    this.availableParents.set(available);
  }

  isDescendant(potentialParent: Department, target: Department): boolean {
    if (!potentialParent.children || potentialParent.children.length === 0) {
      return false;
    }
    
    for (const child of potentialParent.children) {
      if (child.id === target.id) {
        return true;
      }
      if (this.isDescendant(child, target)) {
        return true;
      }
    }
    
    return false;
  }

  onSubmit(): void {
    if (this.form.invalid) {
      return;
    }

    this.loading.set(true);
    this.error.set(null);

    const { newParentId, newOrder } = this.form.value;

    this.departmentService.moveDepartment(this.department.id, {
      newParentId: newParentId || 0, // 0 means no parent (root level)
      newOrder: newOrder
    }).subscribe({
      next: (response) => {
        if (response.success) {
          this.departmentMoved.emit();
          this.close.emit();
        } else {
          this.error.set(response.message || 'Failed to move department');
        }
        this.loading.set(false);
      },
      error: (err) => {
        this.error.set('Failed to move department');
        this.loading.set(false);
        console.error('Error moving department:', err);
      }
    });
  }

  onCancel(): void {
    this.close.emit();
  }

  getFieldError(fieldName: string): string {
    const field = this.form.get(fieldName);
    if (field?.invalid && field?.touched) {
      if (field.errors?.['required']) {
        return this.translate('common.field_required');
      }
      if (field.errors?.['min']) {
        return `${this.translate('common.min_value')} ${field.errors['min'].min}`;
      }
    }
    return '';
  }

  isFieldInvalid(fieldName: string): boolean {
    const field = this.form.get(fieldName);
    return !!(field?.invalid && field?.touched);
  }

  getDepartmentDisplayName(dept: Department): string {
    return this.isRTL() ? dept.nameAr : dept.nameEn;
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }
}
