import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef,
  HostListener,
  signal,
  computed,
  effect,
  AfterViewInit,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslationService } from '../../../services/translation.service';
import {
  DepartmentService,
  Department,
} from '../../../services/department.service';
import * as d3 from 'd3';
import { OrgChart } from 'd3-org-chart';

@Component({
  selector: 'app-org-chart',
  standalone: false,
  templateUrl: './org-chart.component.html',
  styleUrls: ['./org-chart.component.css'],
})
export class OrgChartComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('chartContainer', { static: false })
  chartContainer!: ElementRef<HTMLDivElement>;

  // State
  departments = signal<Department[]>([]);
  loading = signal(true);
  error = signal<string | null>(null);
  selectedDepartment = signal<Department | null>(null);

  // D3 Org Chart
  private chart: OrgChart | null = null;
  private chartData: any[] = [];

  // Level colors for visual hierarchy
  levelColors = [
    '#09365F', // Root level - Dark blue
    '#158638', // Level 1 - Green
    '#0B4B7A', // Level 2 - Blue
    '#1B9A54', // Level 3 - Light green
    '#0E577F', // Level 4 - Dark blue
    '#1FA661', // Level 5 - Bright green
  ];

  constructor(
    private departmentService: DepartmentService,
    public translationService: TranslationService
  ) {
    // Effect to render chart when departments change
    effect(() => {
      if (this.departments().length > 0) {
        setTimeout(() => this.renderChart(), 100);
      }
    });
  }

  ngOnInit(): void {
    this.loadDepartments();
  }

  ngAfterViewInit(): void {
    // Chart will be rendered when departments are loaded
  }

  ngOnDestroy(): void {
    if (this.chart) {
      this.chart = null;
    }
  }

  @HostListener('window:resize')
  onWindowResize(): void {
    if (this.chart) {
      this.chart.render().fit();
    }
  }

  // Transform department data to d3-org-chart format
  private transformDataToChartFormat(departments: Department[]): any[] {
    const chartData: any[] = [];

    const processDepartment = (
      dept: Department,
      parentId: string | null = null
    ) => {
      const nodeData = {
        id: dept.id.toString(),
        parentId: parentId,
        name: this.isRTL() ? dept.nameAr : dept.nameEn,
        level: this.getDepartmentLevel(dept),
      };

      chartData.push(nodeData);

      // Process children recursively
      if (dept.children && dept.children.length > 0) {
        dept.children.forEach((child) => {
          processDepartment(child, dept.id.toString());
        });
      }
    };

    // Process root departments
    const rootDepartments = departments.filter((dept) => !dept.parentId);
    rootDepartments.forEach((rootDept) => {
      processDepartment(rootDept);
    });

    return chartData;
  }

  // Render the d3 org chart
  private renderChart(): void {
    if (!this.chartContainer || this.departments().length === 0) {
      return;
    }

    // Clear previous chart
    if (this.chart) {
      this.chart = null;
    }

    // Transform data
    this.chartData = this.transformDataToChartFormat(this.departments());

    // Clear container
    d3.select(this.chartContainer.nativeElement).selectAll('*').remove();

    // Create new chart
    this.chart = new OrgChart()
      .nodeHeight((d: any) => 120)
      .nodeWidth((d: any) => 220)
      .childrenMargin((d: any) => 40)
      .compactMarginBetween((d: any) => 25)
      .compactMarginPair((d: any) => 20)
      .neighbourMargin((a: any, b: any) => 15)
      .nodeContent((d: any, i: any, arr: any, state: any) => {
        const levelColor = this.getLevelColorForNode(d.data.level);
        const isRoot = d.data.level === 0;

        return `
          <div style='width:${d.width}px;height:${d.height}px;padding:6px;'>
            <div style="
              font-family: 'Inter', sans-serif;
              background: ${
                isRoot
                  ? `linear-gradient(135deg, ${levelColor} 0%, ${levelColor}dd 100%)`
                  : 'linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%)'
              };
              color: ${isRoot ? '#FFFFFF' : '#2c3e50'};
              margin: 0;
              width: ${d.width - 12}px;
              height: ${d.height - 12}px;
              border-radius: 12px;
              border: 2px solid ${isRoot ? 'rgba(255,255,255,0.2)' : '#e1e8ed'};
              box-shadow: ${
                isRoot
                  ? '0 8px 25px rgba(0,0,0,0.15), 0 4px 12px rgba(0,0,0,0.1)'
                  : '0 4px 15px rgba(0,0,0,0.08), 0 2px 8px rgba(0,0,0,0.05)'
              };
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              text-align: center;
              padding: 20px 16px;
              transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
              cursor: pointer;
              position: relative;
              overflow: hidden;
            " onmouseover="this.style.transform='translateY(-4px) scale(1.02)'; this.style.boxShadow='${
              isRoot
                ? '0 12px 35px rgba(0,0,0,0.2), 0 6px 18px rgba(0,0,0,0.15)'
                : '0 8px 25px rgba(0,0,0,0.12), 0 4px 15px rgba(0,0,0,0.08)'
            }';" onmouseout="this.style.transform='translateY(0) scale(1)'; this.style.boxShadow='${
          isRoot
            ? '0 8px 25px rgba(0,0,0,0.15), 0 4px 12px rgba(0,0,0,0.1)'
            : '0 4px 15px rgba(0,0,0,0.08), 0 2px 8px rgba(0,0,0,0.05)'
        }';">
              <div style="
                font-size: 16px;
                font-weight: 700;
                line-height: 1.5;
                word-break: break-word;
                max-width: 100%;
                overflow: visible;
                text-overflow: unset;
                white-space: normal;
                display: block;
                text-shadow: ${isRoot ? '0 1px 2px rgba(0,0,0,0.3)' : 'none'};
                padding: 4px 0;
              ">${d.data.name}</div>
            </div>
          </div>
        `;
      })
      .container(this.chartContainer.nativeElement)
      .data(this.chartData)
      .render();
  }

  // Get level color for node
  private getLevelColorForNode(level: number): string {
    if (level >= this.levelColors.length) {
      return this.levelColors[this.levelColors.length - 1];
    }
    return this.levelColors[level];
  }

  // Public control methods
  fitToScreen(): void {
    if (this.chart) {
      this.chart.render().fit();
    }
  }

  resetTo100(): void {
    if (this.chart) {
      // Use the built-in fullscreen method from d3-org-chart library
      (this.chart as any).fullscreen();
    }
  }

  zoomIn(): void {
    if (this.chart) {
      // Use the built-in zoomIn method from d3-org-chart library
      (this.chart as any).zoomIn();
    }
  }

  zoomOut(): void {
    if (this.chart) {
      // Use the built-in zoomOut method from d3-org-chart library
      (this.chart as any).zoomOut();
    }
  }

  // Toggle between horizontal and compact view
  toggleHorizontal(): void {
    if (this.chart) {
      this.chart.compact(false).render().fit();
    }
  }

  toggleCompact(): void {
    if (this.chart) {
      this.chart.compact(true).render().fit();
    }
  }

  // Expand all departments
  expandAll(): void {
    if (this.chart) {
      // Use the built-in expandAll method from d3-org-chart library
      (this.chart as any).expandAll().fit();
    }
  }

  // Collapse all departments
  collapseAll(): void {
    if (this.chart) {
      // Use the built-in collapseAll method from d3-org-chart library
      (this.chart as any).collapseAll().fit();
    }
  }

  // Language/direction change handler
  onLanguageChange(): void {
    setTimeout(() => {
      this.renderChart();
    }, 100);
  }

  // Translation method for template
  translate(key: string): string {
    return this.translationService.translate(key);
  }

  loadDepartments(): void {
    this.loading.set(true);
    this.error.set(null);

    this.departmentService.getDepartmentsTree().subscribe({
      next: (response) => {
        if (response.success) {
          this.departments.set(this.processDepartments(response.data));
          console.log('Departments loaded:', this.departments());
        } else {
          this.error.set(response.message || 'Failed to load departments');
        }
        this.loading.set(false);
      },
      error: (err) => {
        this.error.set('Failed to load departments');
        this.loading.set(false);
        console.error('Error loading departments:', err);
      },
    });
  }

  private processDepartments(departments: Department[]): Department[] {
    return departments;
  }

  // Calculate department level
  getDepartmentLevel(department: Department): number {
    let level = 0;
    let current = department;

    while (current.parentId) {
      level++;
      const parent = this.findDepartmentById(current.parentId);
      if (!parent) break;
      current = parent;
    }

    return level;
  }

  // Utility methods
  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  getDepartmentTypeName(type: number): string {
    const typeKey = `departments.department_type.${type}`;
    return this.translationService.translate(typeKey);
  }

  getStatusClass(isActive: boolean): string {
    return isActive ? 'status-active' : 'status-inactive';
  }

  getStatusText(isActive: boolean): string {
    const key = isActive
      ? 'orgchart.status.active'
      : 'orgchart.status.inactive';
    return this.translationService.translate(key);
  }

  findDepartmentById(id: number): Department | null {
    const searchInDepartments = (depts: Department[]): Department | null => {
      for (const dept of depts) {
        if (dept.id === id) return dept;
        if (dept.children && dept.children.length > 0) {
          const found = searchInDepartments(dept.children);
          if (found) return found;
        }
      }
      return null;
    };

    return searchInDepartments(this.departments());
  }
}
