import { Component, OnInit, Output, EventEmitter, signal, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';

import { Department, DepartmentService } from '../../../services/department.service';
import { DepartmentActionsComponent } from '../department-actions/department-actions.component';
import { ChangeManagerModalComponent } from '../change-manager-modal/change-manager-modal.component';
import { MoveDepartmentModalComponent } from '../move-department-modal/move-department-modal.component';
import { DepartmentFormComponent } from '../department-form/department-form.component';
import { TranslationService } from '../../../services/translation.service';

@Component({
  selector: 'app-departments-cards-view',
  standalone: false,
  templateUrl: './departments-cards-view.component.html',
  styleUrls: ['./departments-cards-view.component.css']
})
export class DepartmentsCardsViewComponent implements OnInit {
  @Output() refresh = new EventEmitter<void>();

  departments = signal<Department[]>([]);
  loading = signal(false);
  error = signal<string | null>(null);
  
  // Pagination state
  currentPage = signal(1);
  pageSize = signal(10);
  totalCount = signal(0);
  totalPages = signal(0);
  
  // Filter state
  searchTerm = signal('');
  selectedType = signal<number | undefined>(undefined);
  selectedStatus = signal<boolean | undefined>(undefined);
  selectedParentId = signal<number | undefined>(undefined);
  
  // Modal states
  showChangeManagerModal = signal(false);
  showMoveModal = signal(false);
  showEditModal = signal(false);
  showDeleteModal = signal(false);
  selectedDepartment = signal<Department | null>(null);
  departmentToDelete = signal<Department | null>(null);

  deleteModalData = computed(() => ({
    title: this.translate('departments.delete'),
    message: this.translate('departments.confirm_delete'),
    confirmText: this.translate('common.delete'),
    cancelText: this.translate('common.cancel'),
    type: 'delete' as const
  }));

  constructor(
    private departmentService: DepartmentService,
    private translationService: TranslationService
  ) {}

  ngOnInit(): void {
    this.loadDepartments();
  }

  loadDepartments(): void {
    this.loading.set(true);
    this.error.set(null);

    this.departmentService.getDepartments(
      this.currentPage(),
      this.pageSize(),
      this.searchTerm() || undefined,
      this.selectedType(),
      this.selectedStatus(),
      this.selectedParentId()
    ).subscribe({
      next: (response) => {
        if (response.success) {
          this.departments.set(response.data.items);
          this.totalCount.set(response.data.totalCount);
          this.totalPages.set(Math.ceil(response.data.totalCount / this.pageSize()));
        } else {
          this.error.set(response.message || 'Failed to load departments');
        }
        this.loading.set(false);
      },
      error: (err) => {
        this.error.set('Failed to load departments');
        this.loading.set(false);
        console.error('Error loading departments:', err);
      }
    });
  }

  // Pagination methods
  goToPage(page: number): void {
    if (page >= 1 && page <= this.totalPages()) {
      this.currentPage.set(page);
      this.loadDepartments();
    }
  }

  changePageSize(newSize: number): void {
    this.pageSize.set(newSize);
    this.currentPage.set(1); // Reset to first page
    this.loadDepartments();
  }

  nextPage(): void {
    if (this.currentPage() < this.totalPages()) {
      this.goToPage(this.currentPage() + 1);
    }
  }

  previousPage(): void {
    if (this.currentPage() > 1) {
      this.goToPage(this.currentPage() - 1);
    }
  }

  // Search and filter methods
  onSearchChange(term: string): void {
    this.searchTerm.set(term);
    this.currentPage.set(1); // Reset to first page when searching
    this.loadDepartments();
  }

  onTypeChange(type: number | undefined): void {
    this.selectedType.set(type);
    this.currentPage.set(1);
    this.loadDepartments();
  }

  onStatusChange(status: boolean | undefined): void {
    this.selectedStatus.set(status);
    this.currentPage.set(1);
    this.loadDepartments();
  }

  onParentChange(parentId: number | undefined): void {
    this.selectedParentId.set(parentId);
    this.currentPage.set(1);
    this.loadDepartments();
  }

  // Helper methods for pagination display
  getPageNumbers(): number[] {
    const total = this.totalPages();
    const current = this.currentPage();
    const delta = 2; // Number of pages to show on each side of current page
    
    const range = [];
    const rangeWithDots = [];

    for (let i = Math.max(2, current - delta); i <= Math.min(total - 1, current + delta); i++) {
      range.push(i);
    }

    if (current - delta > 2) {
      rangeWithDots.push(1, -1); // -1 represents "..."
    } else {
      rangeWithDots.push(1);
    }

    rangeWithDots.push(...range);

    if (current + delta < total - 1) {
      rangeWithDots.push(-1, total); // -1 represents "..."
    } else if (total > 1) {
      rangeWithDots.push(total);
    }

    return rangeWithDots;
  }

  getStartRecord(): number {
    return (this.currentPage() - 1) * this.pageSize() + 1;
  }

  getEndRecord(): number {
    return Math.min(this.currentPage() * this.pageSize(), this.totalCount());
  }

  openChangeManagerModal(department: Department): void {
    this.selectedDepartment.set(department);
    this.showChangeManagerModal.set(true);
  }

  openMoveModal(department: Department): void {
    this.selectedDepartment.set(department);
    this.showMoveModal.set(true);
  }

  openEditModal(department: Department): void {
    this.selectedDepartment.set(department);
    this.showEditModal.set(true);
  }

  onManagerChanged(): void {
    this.showChangeManagerModal.set(false);
    this.selectedDepartment.set(null);
    this.loadDepartments();
    this.refresh.emit();
  }

  onDepartmentMoved(): void {
    this.showMoveModal.set(false);
    this.selectedDepartment.set(null);
    this.loadDepartments();
    this.refresh.emit();
  }

  onDepartmentUpdated(): void {
    this.showEditModal.set(false);
    this.selectedDepartment.set(null);
    this.loadDepartments();
    this.refresh.emit();
  }

  onDepartmentDeleted(): void {
    this.loadDepartments();
    this.refresh.emit();
  }

  confirmDeleteDepartment(department: Department): void {
    this.departmentToDelete.set(department);
    this.showDeleteModal.set(true);
  }

  onDeleteConfirm(): void {
    if (this.departmentToDelete()) {
      this.deleteDepartment(this.departmentToDelete()!);
      this.showDeleteModal.set(false);
      this.departmentToDelete.set(null);
    }
  }

  onDeleteCancel(): void {
    this.showDeleteModal.set(false);
    this.departmentToDelete.set(null);
  }

  deleteDepartment(department: Department): void {
    this.departmentService.deleteDepartment(department.id).subscribe({
      next: (response) => {
        if (response.success) {
          this.loadDepartments();
          this.refresh.emit();
        } else {
          console.error('Failed to delete department:', response.message);
        }
      },
      error: (err) => {
        console.error('Error deleting department:', err);
      }
    });
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  getDepartmentTypeName(type: number): string {
    const typeKey = `departments.department_type.${type}`;
    return this.translate(typeKey);
  }

  formatDate(dateString: string): string {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString(this.isRTL() ? 'ar-SA' : 'en-US');
  }

  getDepartmentIcon(type: number): string {
    switch (type) {
      case 1: // Authority
        return 'M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z'; // Star icon
      case 2: // Board
        return 'M3 3h18v18H3V3zm5 5h8M8 12h8'; // Board icon
      case 3: // Director General
        return 'M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2M12 3a4 4 0 1 0 0 8 4 4 0 0 0 0-8z'; // Person icon
      case 4: // Office
        return 'M3 21h18M5 21V7l8-4v18M13 9h4v12'; // Building icon
      case 5: // Department
        return 'M3 3h18v18H3V3zM8 12h8M12 8v8'; // Grid icon
      case 6: // Section
        return 'M4 6h16M4 12h16M4 18h16'; // List icon
      case 7: // Unit
        return 'M9 11H5a2 2 0 0 0-2 2v7h18v-7a2 2 0 0 0-2-2h-4M9 11V9a2 2 0 0 1 2-2h2a2 2 0 0 1 2 2v2M9 11h6'; // Box icon
      default:
        return 'M3 3h18v18H3V3zM8 12h8M12 8v8'; // Default grid icon
    }
  }
}
