import { Component, OnInit, signal, computed, HostListener, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';


import { OrgChartComponent } from './org-chart/org-chart.component';
import { DepartmentsCardsViewComponent } from './departments-cards-view/departments-cards-view.component';
import { DepartmentsTableViewComponent } from './departments-table-view/departments-table-view.component';
import { DepartmentFormComponent } from './department-form/department-form.component';
import { TranslationService } from '../../services/translation.service';

export type DepartmentView = 'orgchart' | 'cards' | 'table';

@Component({
  selector: 'app-departments',
  standalone: false,
  templateUrl: './departments.component.html',
  styleUrls: ['./departments.component.css']
})

export class DepartmentsComponent implements OnInit {
  currentView = signal<DepartmentView>('orgchart');
  showViewMenu = signal(false);
  showAddModal = signal(false);

  constructor(private translationService: TranslationService) {}

  ngOnInit(): void {
    // Initialize with org chart view
    this.currentView.set('orgchart');
  }

  setView(view: DepartmentView): void {
    this.currentView.set(view);
    this.showViewMenu.set(false);
  }

  toggleViewMenu(): void {
    this.showViewMenu.update(show => !show);
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick(event: Event): void {
    // Close view menu if clicking outside
    if (this.showViewMenu()) {
      const target = event.target as HTMLElement;
      if (!target.closest('.view-selector')) {
        this.showViewMenu.set(false);
      }
    }
  }

  openAddModal(): void {
    this.showAddModal.set(true);
  }

  closeAddModal(): void {
    this.showAddModal.set(false);
  }

  onDepartmentCreated(): void {
    this.closeAddModal();
    // Refresh current view
    // This will be handled by child components
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }
}
