import { Component, Input, Output, EventEmitter, signal, HostListener } from '@angular/core';
import { CommonModule } from '@angular/common';

import { Department } from '../../../services/department.service';
import { TranslationService } from '../../../services/translation.service';

@Component({
  selector: 'app-department-actions',
  standalone: false,
  templateUrl: './department-actions.component.html',
  styleUrls: ['./department-actions.component.css']
})
export class DepartmentActionsComponent {
  @Input() department!: Department;
  @Output() changeManager = new EventEmitter<Department>();
  @Output() move = new EventEmitter<Department>();
  @Output() edit = new EventEmitter<Department>();
  @Output() delete = new EventEmitter<Department>();

  showMenu = signal(false);

  constructor(private translationService: TranslationService) {}

  toggleMenu(): void {
    this.showMenu.update(show => !show);
  }

  closeMenu(): void {
    this.showMenu.set(false);
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick(event: Event): void {
    // Close menu if clicking outside
    if (this.showMenu()) {
      const target = event.target as HTMLElement;
      if (!target.closest('.actions-container')) {
        this.showMenu.set(false);
      }
    }
  }

  onAction(action: string): void {
    this.closeMenu();
    
    switch (action) {
      case 'changeManager':
        this.changeManager.emit(this.department);
        break;
      case 'move':
        this.move.emit(this.department);
        break;
      case 'edit':
        this.edit.emit(this.department);
        break;
      case 'delete':
        this.delete.emit(this.department);
        break;
    }
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }
}
