import { Component, Input, Output, EventEmitter, OnInit, signal, computed } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormBuilder, FormGroup, Validators } from '@angular/forms';

import { Department, DepartmentService } from '../../../services/department.service';
import { TranslationService } from '../../../services/translation.service';
import { UsersService } from '../../../services/users.service';
import { User } from '../../../models/user.model';

@Component({
  selector: 'app-change-manager-modal',
  standalone: false,
  templateUrl: './change-manager-modal.component.html',
  styleUrls: ['./change-manager-modal.component.css']
})
export class ChangeManagerModalComponent implements OnInit {
  @Input() department!: Department;
  @Input() isOpen = false;
  @Output() close = new EventEmitter<void>();
  @Output() managerChanged = new EventEmitter<void>();

  form!: FormGroup;
  loading = signal(false);
  error = signal<string | null>(null);
  
  // Real users from the system
  users = signal<User[]>([]);
  filteredUsers = signal<User[]>([]);
  searchTerm = signal('');

  constructor(
    private fb: FormBuilder,
    private departmentService: DepartmentService,
    private translationService: TranslationService,
    private usersService: UsersService
  ) {}

  ngOnInit(): void {
    this.initForm();
    this.loadUsers();
  }

  loadUsers(): void {
    this.loading.set(true);
    this.usersService.getUsers().subscribe({
      next: (response) => {
        if (response.success && response.data && response.data.items) {
          this.users.set(response.data.items);
          this.filteredUsers.set(response.data.items);
        } else {
          console.warn('Unexpected response structure for users:', response);
          this.users.set([]);
          this.filteredUsers.set([]);
        }
        this.loading.set(false);
      },
      error: (err) => {
        console.error('Error loading users:', err);
        this.error.set('Failed to load users');
        this.loading.set(false);
      }
    });
  }

  initForm(): void {
    this.form = this.fb.group({
      managerUserId: ['', Validators.required]
    });

    if (this.department.managerUserId) {
      this.form.patchValue({
        managerUserId: this.department.managerUserId
      });
    }
  }

  onSearch(event: Event): void {
    const term = (event.target as HTMLInputElement).value.toLowerCase();
    this.searchTerm.set(term);
    
    if (!term) {
      this.filteredUsers.set(this.users());
    } else {
      const filtered = this.users().filter(user => 
        user.firstName?.toLowerCase().includes(term) ||
        user.lastName?.toLowerCase().includes(term) ||
        user.email?.toLowerCase().includes(term)
      );
      this.filteredUsers.set(filtered);
    }
  }

  onSubmit(): void {
    if (this.form.invalid) {
      return;
    }

    this.loading.set(true);
    this.error.set(null);

    const managerUserId = this.form.value.managerUserId;

    this.departmentService.changeManager(this.department.id, { managerUserId }).subscribe({
      next: (response) => {
        if (response.success) {
          this.managerChanged.emit();
          this.close.emit();
        } else {
          this.error.set(response.message || 'Failed to change manager');
        }
        this.loading.set(false);
      },
      error: (err) => {
        console.error('Error changing manager:', err);
        this.error.set('Failed to change manager');
        this.loading.set(false);
      }
    });
  }

  onCancel(): void {
    this.close.emit();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  getUserDisplayName(user: User): string {
    if (user.firstName && user.lastName) {
      return `${user.firstName} ${user.lastName}`;
    }
    return user.email || 'Unknown User';
  }
}
